import { HText } from "@macashipo/mlib"
const AText = {
  //bo sung compareTextForSearch, truong hop search ki tu []
  compareTextForSearch(text = "", textSearch = "", opts) {
    if (textSearch.indexOf("[") > -1 || textSearch.indexOf("]") > -1) {
      let _textSearch = textSearch.toLowerCase()
      let _text = text.toLowerCase()
      if (_text.indexOf(_textSearch) > -1) {
        return true
      } else {
        return false
      }
    }
    return HText.compareTextForSearch(text, textSearch, opts)
  },
}

export default AText
