import { Component } from "react"
import styled from "styled-components"
import { ApiGeneric } from "src/services/apiServices"
import { HUtils } from "@macashipo/mlib"
const WrapLayout = styled.div`
  height: 100%;
  overflow: auto;
  padding: 5px;
  & .a-authenticator {
    background: whitesmoke;
    padding: 10px;
    border-radius: 5px;
    & .a-title {
      font-weight: bold;
      margin-bottom: 10px;
    }
  }
`

class MUIFormCollection extends Component {
  constructor(props) {
    super(props)
    this.state = {}
    const collection = this.props.collection || {}
    this._orgCollection = { ...collection }
  }
  componentDidMount() {}
  requestUpdateCollection = () => {
    const collection = this.props.collection || {}
    const fnList = this.props.fnList || {}
    HUtils.runFuntion(fnList, "requestSaveCollection", [
      collection,
      this._orgCollection,
    ])
  }
  render() {
    const collection = this.props.collection || {}
    return (
      <WrapLayout>
        <div>
          <form
            onSubmit={e => {
              e.preventDefault()
            }}
          >
            <div className="form-row">
              <div className="form-group col-md-6">
                <label for="inputEmail4">Name</label>
                <input
                  type="text"
                  className="form-control"
                  defaultValue={collection.Name}
                  onChange={ev => {
                    collection.Name = ev.currentTarget.value
                  }}
                  id="ipName"
                  placeholder="Name"
                />
              </div>
              <div className="form-group col-md-6">
                <label for="inputPassword4">UniqueId</label>
                <input
                  type="text"
                  className="form-control"
                  defaultValue={collection.UniqueId}
                  readOnly
                  id="ipUniqueId"
                  placeholder="UniqueId"
                />
              </div>
            </div>
            <div className="form-group">
              <label for="inputAddress">Base URL</label>
              <input
                type="text"
                className="form-control"
                defaultValue={collection.BaseUrl}
                onChange={ev => {
                  collection.BaseUrl = ev.currentTarget.value
                }}
                id="ipBaseUrl"
                placeholder="BaseUrl"
              />
            </div>
            <div className="form-group">
              <label for="inputAddress2">Description</label>
              <textarea
                type="text"
                className="form-control"
                defaultValue={collection.Description}
                onChange={ev => {
                  collection.Description = ev.currentTarget.value
                }}
                id="ipDescription"
                placeholder="Description"
              />
            </div>
            <div className="form-group">
              <label for="ipNote">Note</label>
              <textarea
                type="text"
                className="form-control"
                defaultValue={collection.Note}
                onChange={ev => {
                  collection.Note = ev.currentTarget.value
                }}
                id="ipNote"
                placeholder="Note"
              />
            </div>
            <div className="a-authenticator">
              <div className="a-title">Authenticator</div>
              <div>
                <div className="form-group row">
                  <label for="ipUserName" className="col-sm-2 col-form-label">
                    User Name
                  </label>
                  <div className="col-sm-10">
                    <input
                      type="text"
                      className="form-control"
                      defaultValue={collection.UserName}
                      onChange={ev => {
                        collection.UserName = ev.currentTarget.value
                      }}
                      id="ipUserName"
                      placeholder="UserName"
                    />
                  </div>
                </div>
                <div className="form-group row">
                  <label for="ipPassword" className="col-sm-2 col-form-label">
                    Password
                  </label>
                  <div className="col-sm-10">
                    <input
                      type="text"
                      className="form-control"
                      defaultValue={collection.Password}
                      onChange={ev => {
                        collection.Password = ev.currentTarget.value
                      }}
                      id="ipPassword"
                      placeholder="Password"
                    />
                  </div>
                </div>
                <div className="form-group row">
                  <label
                    for="ipAccessToken"
                    className="col-sm-2 col-form-label"
                  >
                    Access Token
                  </label>
                  <div className="col-sm-10">
                    <textarea
                      type="text"
                      className="form-control"
                      defaultValue={collection.AccessToken}
                      readOnly
                      id="ipAccessToken"
                      placeholder="AccessToken"
                    />
                  </div>
                </div>
                <div className="form-group row">
                  <label for="ipLogin" className="col-sm-2 col-form-label">
                    {` `}
                  </label>
                  <div className="col-sm-10">
                    <button
                      className="btn btn-primary"
                      onClick={ev => {
                        console.log("click btn sign in")
                        HUtils.runFuntion(
                          this.props.fnList,
                          "requestLoginGetAccessToken",
                          [collection]
                        )
                        ev.preventDefault()
                      }}
                    >
                      Sign in to get new access token
                    </button>
                  </div>
                </div>
              </div>
            </div>
            <div>
              <hr />
            </div>
            <button
              className="btn btn-primary"
              onClick={() => {
                this.requestUpdateCollection()
              }}
            >
              Save
            </button>
          </form>
        </div>
      </WrapLayout>
    )
  }
}

export default MUIFormCollection
