import { Component } from "react"
import styled from "styled-components"
import { ApiGeneric } from "src/services/apiServices"
import { apiGeneric as ApiGenericPromise } from "src/core/helpers/api"
import Tabs from "react-responsive-tabs"
import { MyPage, MyLib, HUtils, MyUI, HLink } from "@macashipo/mlib"
import classNames from "classnames"
import MUIFormCollection from "../MyUI/prj_api_form_collection"
import { MyModal, Popover } from "src/core/components"
import { fnListModal, fnListPopover } from "src/helpers/fnList"
import { debounce } from "src/core/helpers/utils"
import AText from "src/helpers/AText"

function PrettyJsonString(jsonString) {
  let _s = jsonString
  try {
    _s = JSON.stringify(JSON.parse(jsonString), null, 2)
  } catch (error) {}
  return _s
}
class MP_PRJ_Info extends Component {
  constructor(props) {
    super(props)
    this.state = {
      Data: [],
      open: true,
      listCollection: null,
      listRequestOfCollection: null,
      listRequestFiltered: null,
      currentRequest: null,
      currentCollection: null,
      isRunning: false,
      loading: false,
      loadingLeft: false,
      showSearchRequest: false,
    }
    this._refResponseCode = null
    this._refRequestBody = null
    this._refRequestHeader = null
    this._refRequestNote = null
    this._keyFormCollection = new Date().getTime()
    this._paramsurl = HLink.getAllParamOnUrl() || {}
    this._defaultApi = this._paramsurl["api"]
    this._projectId = HUtils.runFuntion(props.fnList, "getCurrentProjectId", [])
    this._keyCResponseCode = new Date().getTime()
    this._keyRequestion = new Date().getTime()
    this._handlerSearchRequest = debounce(s => {
      console.warn("search:", s)
      if (
        this.state.listRequestOfCollection &&
        this.state.listRequestOfCollection.length > 0
      ) {
        let _arrFilter = this.state.listRequestOfCollection.filter((v, i) => {
          let _textFilter = `${v.Name} ${v.Url} ${v.SubUrl}`
          console.warn("search:", _textFilter, s)
          if (
            AText.compareTextForSearch(_textFilter, s, { searchKoDau: true })
          ) {
            return true
          }
          return false
        })
        this.setState(
          {
            listRequestFiltered: _arrFilter,
          },
          () => {
            console.log(_arrFilter)
          }
        )
      }
    }, 300)
  }
  componentDidMount() {
    this._fnList = {}
    this._tabsRequest = [
      {
        title: (
          <div>
            Body{" "}
            <i
              onClick={() => {
                console.warn("click expand")
                MyModal.showFromMyUI(
                  {
                    type: "json_viewer",
                    data: this.state.currentRequest?.Body,
                  },
                  {
                    title: "Request Body",
                    size: "full",
                  }
                )
              }}
              className="fa fa-expand"
            />
          </div>
        ),
        getContent: () => {
          return (
            <div
              style={{
                maxHeight: "100%",
                overflow: "hidden",
                overflowY: "auto",
              }}
            >
              <MyLib
                type={"code_mirror"}
                innerRef={r => {
                  console.warn("innerRef")
                  this._refRequestBody = r
                }}
                value={PrettyJsonString(this.state.currentRequest?.Body || "")}
                options={{
                  // mode: "javascript",
                  mode: {
                    name: "javascript",
                    json: true,
                    statementIndent: 2,
                  },
                  lineNumbers: true,
                  lineWrapping: true,
                  indentWithTabs: false,
                  tabSize: 2,
                  theme: "default",
                }}
              />
            </div>
          )
        },
      },
      {
        title: "Headers",
        getContent: () => {
          return (
            <div
              style={{
                maxHeight: "100%",
                overflow: "hidden",
                overflowY: "auto",
              }}
            >
              <MyLib
                type={"code_mirror"}
                innerRef={r => {
                  console.warn("innerRef")
                  this._refRequestHeader = r
                }}
                value={PrettyJsonString(
                  this.state.currentRequest?.Header || ""
                )}
                options={{
                  // mode: "javascript",
                  mode: {
                    name: "javascript",
                    json: true,
                    statementIndent: 2,
                  },
                  lineNumbers: true,
                  lineWrapping: true,
                  indentWithTabs: false,
                  tabSize: 2,
                  theme: "default",
                }}
              />
            </div>
          )
        },
      },
      {
        title: "Test Cases",
        getContent: () => {
          return <MyUI type="prj_api_testcases" fnList={this._fnList} />
        },
      },
      {
        title: "Setting",
        getContent: () => {
          return <MyUI type="prj_api_setting" fnList={this._fnList} />
        },
      },
      {
        title: "Note",
        getContent: () => {
          return (
            <div
              style={{
                maxHeight: "100%",
                overflow: "hidden",
                overflowY: "auto",
              }}
            >
              <MyLib
                type={"code_mirror"}
                innerRef={r => {
                  console.warn("innerRef")
                  this._refRequestNote = r
                }}
                value={this.state.currentRequest?.Note || ""}
                options={{
                  // mode: "javascript",
                  mode: {},
                  lineNumbers: true,
                  lineWrapping: true,
                  indentWithTabs: false,
                  tabSize: 2,
                  theme: "default",
                }}
              />
            </div>
          )
        },
      },
    ]
    this._tabsResponse = [
      {
        title: (
          <div>
            Result{" "}
            <i
              onClick={() => {
                console.warn("click expand")
                MyModal.showFromMyUI(
                  {
                    type: "json_viewer",
                    data: this.state.currentRequest?.ResponeText,
                  },
                  {
                    title: "Response Result",
                    size: "full",
                  }
                )
              }}
              className="fa fa-expand"
            />
          </div>
        ),
        getContent: () => {
          return (
            <div className="a-res-result">
              <MyLib
                type={"code_mirror"}
                innerRef={r => {
                  console.warn("innerRef")
                  this._refResponseCode = r
                }}
                key={this._keyCResponseCode}
                value={PrettyJsonString(
                  this.state.currentRequest?.ResponeText || ""
                )}
                options={{
                  // mode: "javascript",
                  mode: {
                    name: "javascript",
                    json: true,
                    statementIndent: 2,
                  },
                  lineNumbers: true,
                  lineWrapping: true,
                  indentWithTabs: false,
                  tabSize: 2,
                  theme: "default",
                }}
              />
            </div>
          )
        },
      },
    ]
    this.requestCollections()
    if (this._defaultApi) {
      this.requestDetail()
    }
  }

  // requestOptions = () => {
  //   ApiGeneric({
  //     request: {
  //       method: "POST",
  //       path: "/api/v1/ApiDocument_Demo",
  //       name: "List",
  //     },
  //     data: {},
  //     successCallBack: response => {},
  //     errorCallBack: err => {
  //       console.log(err)
  //     },
  //   })
  // }

  requestDetail = () => {
    this.setState(
      {
        loading: true,
      },
      () => {
        ApiGeneric({
          request: {
            method: "GET",
            path: "ProjectAPIRequest",
            name: `Detail/${this._defaultApi}`,
          },
          data: {},
          successCallBack: response => {
            console.log("res", response)
            let _request = null
            if (response.Data) {
              _request = response.Data
            }
            this.setState({
              loading: false,
              currentRequest: _request,
            })
          },
          errorCallBack: err => {
            console.log(err)
            this.setState({
              loading: false,
            })
          },
        })
      }
    )
  }
  requestCollections = () => {
    this.setState(
      {
        loading: true,
      },
      () => {
        ApiGeneric({
          request: {
            method: "POST",
            path: "/api/v1/ProjectAPIDomain",
            name: "List",
          },
          data: {
            ProjectId: HUtils.runFuntion(
              this.props.fnList,
              "getCurrentProjectId",
              []
            ),
          },
          successCallBack: response => {
            if (response.Data.Data) {
              this.setState({
                listCollection: response.Data.Data,
                loading: false,
              })
            }
          },
          errorCallBack: err => {
            console.log(err)
            this.setState({
              loading: false,
            })
          },
        })
      }
    )
  }

  requestSaveCollection = (collection, orgCollection) => {
    let _values = []
    if (collection) {
      _values = Object.keys(collection)
        .filter((v, i) => {
          return (
            [
              "Name",
              "BaseUrl",
              "Description",
              "UserName",
              "Password",
              "Note",
            ].indexOf(v) > -1
          )
        })
        .filter((v, i) => {
          return collection[v] != orgCollection[v]
        })
        .map((v, i) => {
          return {
            FieldName: v,
            NewValue: collection[v],
          }
        })
      console.log("_values:", _values)
      // return
      ApiGeneric({
        request: {
          method: "POST",
          path: "ProjectAPIDomain",
          name: "UpdateFields",
        },
        data: {
          Id: collection.Id,
          Values: _values,
        },
        successCallBack: response => {
          this.forceUpdate()
        },
        errorCallBack: err => {
          console.log(err)
        },
      })
    }
  }

  requestSaveRequestion = (requestion, values, cbSuccess) => {
    let _values = values || []
    if (requestion) {
      console.log("_values:", _values)
      // return
      ApiGeneric({
        request: {
          method: "POST",
          path: "ProjectAPIRequest",
          name: "UpdateFields",
        },
        data: {
          Id: requestion.Id,
          Values: _values,
        },
        successCallBack: response => {
          if (cbSuccess) {
            cbSuccess()
          }
          if (response.Data && response.Data.Id == requestion.Id) {
            for (let k of Object.keys(response.Data)) {
              requestion[k] = response.Data[k]
            }
          }
          this.forceUpdate()
        },
        errorCallBack: err => {
          console.log(err)
        },
      })
    }
  }

  requestLoginGetAccessToken = collection => {
    if (collection) {
      ApiGeneric({
        request: {
          method: "POST",
          path: "ProjectAPIDomain",
          name: `Login/${collection.Id}`,
        },
        data: {},
        successCallBack: response => {
          if (response && response.Data) {
            for (let k of Object.keys(response.Data)) {
              collection[k] = response.Data[k]
            }
          }
          this._keyFormCollection = new Date().getTime()
          this.forceUpdate()
        },
        errorCallBack: err => {
          console.log(err)
        },
      })
    }
  }

  requestRequestion = () => {
    const { currentCollection } = this.state
    if (currentCollection) {
      ApiGeneric({
        request: {
          method: "POST",
          path: "/api/v1/ProjectAPIRequest",
          name: "List",
        },
        data: {
          ProjectAPIDomainId: currentCollection.Id,
        },
        successCallBack: response => {
          if (response.Data.Data) {
            this.setState({
              listRequestOfCollection: response.Data.Data,
              loadingLeft: false,
            })
          } else {
            this.setState({
              listRequestOfCollection: [],
              loadingLeft: false,
            })
          }
        },
        errorCallBack: err => {
          console.log(err)
          this.setState({
            loadingLeft: false,
          })
        },
      })
    }
  }

  requestCloneRequestion = (requestion = {}) => {
    MyModal.showConfirm({
      title: "Duplicate requestion",
      msgHtml: `Are you sure you want to duplicate this requestion?`,
      colorBtnOK: "primary",
      titleBtnOK: "Duplicate",
      onClickOK: cModal => {
        fnListModal.showLoadingModal(cModal._fnList, [])
        ApiGenericPromise({
          method: "POST",
          url: `ProjectAPIRequest/Duplicate/${requestion.Id}`,
          data: {},
        })
          .then(r => {
            if (r.Data) {
              var listRequestOfCollection =
                this.state.listRequestOfCollection || []
              listRequestOfCollection.push(r.Data)
              this.setState({
                currentRequest: r.Data,
              })
            }
            fnListModal.hideLoadingModal(cModal._fnList, [])
            fnListModal.hideModal(cModal._fnList, [])
          })
          .catch(err => {
            fnListModal.hideLoadingModal(cModal._fnList, [])
          })
      },
    })
  }
  requestDeleteCollection = (collection = {}) => {
    MyModal.showConfirm({
      title: "Delete collection",
      msgHtml: `Are you sure you want to delete this collection?`,
      colorBtnOK: "danger",
      titleBtnOK: "Delete",
      onClickOK: cModal => {
        fnListModal.showLoadingModal(cModal._fnList, [])
        ApiGenericPromise({
          method: "POST",
          url: `ProjectAPIDomain/Delete/${collection.Id}`,
          data: {},
        })
          .then(r => {
            var listCollection = this.state.listCollection || []
            for (let i = 0; i < listCollection.length; i++) {
              if (listCollection[i].Id == collection.Id) {
                listCollection.splice(i, 1)
              }
            }
            this.setState({
              currentCollection: null,
            })
            fnListModal.hideLoadingModal(cModal._fnList, [])
            fnListModal.hideModal(cModal._fnList, [])
          })
          .catch(err => {
            fnListModal.hideLoadingModal(cModal._fnList, [])
          })
      },
    })
  }
  requestDeleteRequestion = (requestion = {}) => {
    MyModal.showConfirm({
      title: "Delete requestion",
      msgHtml: `Are you sure you want to delete this requestion?`,
      colorBtnOK: "danger",
      titleBtnOK: "Delete",
      onClickOK: cModal => {
        fnListModal.showLoadingModal(cModal._fnList, [])
        ApiGenericPromise({
          method: "POST",
          url: `ProjectAPIRequest/Delete/${requestion.Id}`,
          data: {},
        })
          .then(r => {
            var listRequestOfCollection =
              this.state.listRequestOfCollection || []
            for (let i = 0; i < listRequestOfCollection.length; i++) {
              if (listRequestOfCollection[i].Id == requestion.Id) {
                listRequestOfCollection.splice(i, 1)
              }
            }
            this.setState({
              currentRequest: null,
            })
            fnListModal.hideLoadingModal(cModal._fnList, [])
            fnListModal.hideModal(cModal._fnList, [])
          })
          .catch(err => {
            fnListModal.hideLoadingModal(cModal._fnList, [])
          })
      },
    })
  }
  requestAddCollection = () => {
    ApiGeneric({
      request: {
        method: "POST",
        path: "ProjectAPIDomain",
        name: "Add",
      },
      data: {
        ProjectId: this._projectId,
      },
      successCallBack: response => {
        if (response.Data) {
          var listCollection = this.state.listCollection || []
          listCollection.unshift(response.Data)
          HLink.clearParamOnUrl("api")
          this.setState(
            {
              listCollection: listCollection,
              listRequestOfCollection: null,
              currentCollection: listCollection[0],
              currentRequest: null,
            },
            () => {
              this.requestRequestion()
            }
          )
        }
      },
      errorCallBack: err => {
        console.log(err)
      },
    })
  }
  requestAddRequestion = () => {
    const { currentCollection } = this.state
    if (currentCollection) {
      ApiGeneric({
        request: {
          method: "POST",
          path: "ProjectAPIRequest",
          name: "Add",
        },
        data: {
          ProjectAPIDomainId: currentCollection.Id,
        },
        successCallBack: response => {
          if (response.Data) {
            var listRequestOfCollection =
              this.state.listRequestOfCollection || []
            listRequestOfCollection.push(response.Data)
            this.setState({
              currentRequest: response.Data,
            })
          }
        },
        errorCallBack: err => {
          console.log(err)
        },
      })
    }
  }
  requestRun = (request = null) => {
    //save fater run
    if (this._refRequestBody && this._refRequestBody.editor) {
      request.Body = this._refRequestBody.editor.getValue()
    }
    if (this._refRequestHeader && this._refRequestHeader.editor) {
      request.Header = this._refRequestHeader.editor.getValue()
    }
    if (this._refRequestNote && this._refRequestNote.editor) {
      request.Note = this._refRequestNote.editor.getValue()
    }

    //
    if (request) {
      this.setState(
        {
          isRunning: true,
        },
        () => {
          ApiGeneric({
            request: {
              method: "POST",
              path: "/api/v1/ProjectAPIRequest",
              name: `RunApi/${request.Id}`,
            },
            data: {
              Request: request,
              // ...request,
            },
            successCallBack: response => {
              if (response.Data) {
                this.setState({ currentRequest: response.Data })
              }
              this.setState({
                isRunning: false,
              })
            },
            errorCallBack: err => {
              console.log(err)
              this.setState({
                isRunning: false,
              })
            },
          })
        }
      )
    }
  }

  requestRunApi = (request = null, { runAndSave } = {}) => {
    //save fater run
    if (this._refRequestBody && this._refRequestBody.editor) {
      request.Body = this._refRequestBody.editor.getValue()
    }
    if (this._refRequestHeader && this._refRequestHeader.editor) {
      request.Header = this._refRequestHeader.editor.getValue()
    }
    if (this._refRequestNote && this._refRequestNote.editor) {
      request.Note = this._refRequestNote.editor.getValue()
    }

    //
    if (request) {
      let _data = {}
      for (let k of [
        "Id",
        "SubUrl",
        "Header",
        "Body",
        "AccessToken",
        "HttpMethod",
        "IsNeedAuthorization",
        "BaseUrl",
      ]) {
        _data[k] = request[k]
      }
      this.setState(
        {
          isRunning: true,
        },
        () => {
          ApiGeneric({
            request: {
              method: "POST",
              path: "/api/v1/ProjectAPIRequest",
              name: runAndSave === true ? `RunApiAndSave` : `RunApi`,
            },
            data: {
              ..._data,
            },
            successCallBack: response => {
              console.warn("res:", response)
              if (response.Data) {
                let _listRequestionOfCollection =
                  this.state.listRequestOfCollection || []
                let _currentRequest = null
                let _keyNeedUpdate = [
                  "ResponeStatus",
                  "ResponeMilliseconds",
                  "ResponeText",
                ]
                //co chon collection
                if (_listRequestionOfCollection.length > 0) {
                  for (let i = 0; i < _listRequestionOfCollection.length; i++) {
                    let _request = _listRequestionOfCollection[i]
                    if (_request.Id == response.Data.Id) {
                      for (let k of _keyNeedUpdate) {
                        _listRequestionOfCollection[i][k] = response.Data[k]
                      }
                      _currentRequest = _listRequestionOfCollection[i]
                      break
                    }
                  }
                } else {
                  _currentRequest = request
                  for (let k of _keyNeedUpdate) {
                    _currentRequest[k] = response.Data[k]
                  }
                }
                this._keyRequestion = new Date().getTime()
                this.setState({
                  currentRequest: _currentRequest,
                  listRequestOfCollection: _listRequestionOfCollection,
                })
              }
              this.setState({
                isRunning: false,
              })
            },
            errorCallBack: err => {
              console.log(err)
              this.setState({
                isRunning: false,
              })
            },
          })
        }
      )
    }
  }

  _renderListCollection = () => {
    const { listCollection } = this.state
    let _ui = []
    if (listCollection) {
      _ui = listCollection.map((v, i) => {
        return (
          <div className="a-item-collection" key={i}>
            <div
              className="a-name"
              onClick={() => {
                console.log("click:", v)
                this.setState(
                  {
                    currentCollection: v,
                    loadingLeft: true,
                  },
                  () => {
                    this.requestRequestion()
                  }
                )
              }}
            >
              {v.Name || v.BaseUrl || "No Name Collection"}
            </div>
            <Popover
              overlay={propsOverlay => {
                return (
                  <div
                    style={{
                      minWidth: "200px",
                      maxHeight: "300px",
                      maxWidth: "300px",
                    }}
                  >
                    <MyUI
                      sourceData={[
                        {
                          Text: "Delete",
                          action: () => {
                            this.requestDeleteCollection(v)
                          },
                        },
                      ]}
                      type="actions_menu"
                      onClick={item => {
                        console.warn("onClick:", item)
                        if (item.action) {
                          item.action()
                        }
                        fnListPopover.hidePopover(propsOverlay)
                      }}
                    />
                  </div>
                )
              }}
              overlayClassName="m-popover"
              placement="bottom"
              destroyTooltipOnHide={true}
            >
              <div className="a-item-action">
                <i className="fa fa-ellipsis-v" />
              </div>
            </Popover>
          </div>
        )
      })
    }
    return _ui
  }
  _renderListRequestion = () => {
    const { listRequestOfCollection, currentCollection, currentRequest } =
      this.state
    let _ui = []
    let _listRequest = listRequestOfCollection
    if (this.state.showSearchRequest) {
      if (this.state.listRequestFiltered == null) {
        _listRequest = listRequestOfCollection
      } else {
        _listRequest = this.state.listRequestFiltered || []
      }
    }
    if (_listRequest) {
      _ui = _listRequest.map((v, i) => {
        return (
          <div
            title={`${v.Name != null ? v.Name + ": " : ""} ${v.SubUrl || ""}`}
            className={classNames("a-item-request", {
              "a-selected": currentRequest != null && currentRequest.Id == v.Id,
            })}
            key={i}
            onClick={() => {
              console.log("click:", v)
              this.setState(
                {
                  currentRequest: v,
                },
                () => {
                  HLink.updateParamOnUrl("api", v.UniqueId)
                }
              )
            }}
          >
            <div className="a-method">{v.HttpMethod}</div>
            <div className="a-item-main">
              {v.Name || v.SubUrl || "No Name Request"}
            </div>
            <Popover
              overlay={propsOverlay => {
                return (
                  <div
                    style={{
                      minWidth: "200px",
                      maxHeight: "300px",
                      maxWidth: "300px",
                    }}
                  >
                    <MyUI
                      sourceData={[
                        {
                          Text: "Rename",
                          action: () => {
                            MyModal.showMyDialogWithInput(
                              {
                                placeHolder: "Name",
                                defaultValue: v.Name || "",
                              },
                              {
                                title: "Change Name",
                                onClickOK: cModal => {
                                  console.log("ok", cModal.getCustomData())
                                  let _newValue = cModal.getCustomData()
                                  if (_newValue != v.Name) {
                                    cModal.showLoading && cModal.showLoading()
                                    this.requestSaveRequestion(
                                      v,
                                      [
                                        {
                                          FieldName: "Name",
                                          NewValue: _newValue,
                                        },
                                      ],
                                      () => {
                                        cModal.hide && cModal.hide()
                                      }
                                    )
                                  }
                                },
                              }
                            )
                          },
                        },
                        {
                          Text: "Clone",
                          action: () => {
                            this.requestCloneRequestion(currentRequest)
                          },
                        },
                        {
                          Text: "Delete",
                          action: () => {
                            this.requestDeleteRequestion(currentRequest)
                          },
                        },
                      ]}
                      type="actions_menu"
                      onClick={item => {
                        console.warn("onClick:", item)
                        if (item.action) {
                          item.action()
                        }
                        fnListPopover.hidePopover(propsOverlay)
                      }}
                    />
                  </div>
                )
              }}
              overlayClassName="m-popover"
              placement="bottom"
              destroyTooltipOnHide={true}
            >
              <div className="a-item-action">
                <i className="fa fa-ellipsis-v" />
              </div>
            </Popover>
          </div>
        )
      })
      if (_listRequest && _listRequest.length == 0) {
        if (this.state.showSearchRequest) {
          _ui = [
            <div key="emptysearch" className="a-lrq-empty">
              Nothing search found!
            </div>,
          ]
        } else {
          _ui = [
            <div key="emptysearch" className="a-lrq-empty">
              No request!
            </div>,
          ]
        }
      }
    } else {
      _ui = [
        <div key={"no"} className="a-lrq-empty">
          No request
        </div>,
      ]
    }
    _ui.unshift(
      <div
        key={"master"}
        className={classNames("a-header-collection", {
          "a-selected": currentCollection != null && currentRequest == null,
        })}
        onClick={() => {
          this.setState(
            {
              currentRequest: null,
            },
            () => {
              HLink.clearParamOnUrl("api")
            }
          )
        }}
      >
        <div>
          {currentCollection.Name || currentCollection.BaseUrl || "No Name"}
        </div>
      </div>
    )
    return _ui
  }

  _onChangeRequestion = ({ fieldName, value }) => {
    // this.requestSaveRequestion(this.state.currentRequest, [
    //   { FieldName: fieldName, NewValue: value },
    // ])
  }
  render() {
    const {
      currentRequest,
      currentCollection,
      listCollection,
      listRequestOfCollection,
      isRunning,
    } = this.state || {}
    const url = Request.Url
    const fnList = {}

    console.log("render:", listCollection)
    return (
      <WrapLayout>
        <div className="prjapi-wrap">
          <div className="prjapi-left">
            {currentCollection != null ? (
              <div className="a-header">
                {this.state.showSearchRequest == true ? (
                  <div
                    style={{
                      display: "flex",
                      alignItems: "center",
                      width: "100%",
                    }}
                  >
                    <input
                      type="text"
                      className="form-control"
                      autoFocus
                      placeholder="Search..."
                      onChange={ev => {
                        console.warn("onChange:", ev.currentTarget.value)
                        this._handlerSearchRequest(ev.currentTarget.value)
                      }}
                    />
                  </div>
                ) : (
                  <div style={{ display: "flex", alignItems: "center" }}>
                    <div
                      className="a-back"
                      style={{ paddingRight: "5px" }}
                      onClick={() => {
                        this.setState({
                          // currentRequest: null,
                          currentCollection: null,
                          listRequestOfCollection: null,
                        })
                      }}
                    >
                      <i className="fa fa-chevron-left" />
                    </div>
                    <div>List Request</div>
                  </div>
                )}
                {this.state.showSearchRequest == true ? (
                  <div>
                    <i
                      className="fa fa-times a-btn-search"
                      onClick={() => {
                        this.setState({
                          showSearchRequest: false,
                          listRequestFiltered: null,
                        })
                      }}
                    />
                  </div>
                ) : (
                  <div>
                    <i
                      className="fa fa-search a-btn-search"
                      style={{}}
                      onClick={() => {
                        this.setState({
                          showSearchRequest: true,
                        })
                      }}
                    />
                    <button
                      className="btn btn-primary"
                      onClick={() => {
                        this.requestAddRequestion()
                      }}
                    >
                      + Request
                    </button>
                  </div>
                )}
              </div>
            ) : (
              <div className="a-header">
                <div>List Collection</div>
                <div>
                  <button
                    className="btn btn-primary"
                    onClick={() => {
                      this.requestAddCollection()
                    }}
                  >
                    + Collection
                  </button>
                </div>
              </div>
            )}
            <div className="a-list">
              {listCollection != null &&
                currentCollection == null &&
                this._renderListCollection()}
              {listCollection != null &&
                currentCollection != null &&
                listRequestOfCollection != null &&
                this._renderListRequestion()}
              {this.state.loading == true || this.state.loadingLeft == true ? (
                <div style={{ textAlign: "center", marginTop: "1rem" }}>
                  {" "}
                  <i
                    className="fa fa-spin fa-spinner"
                    style={{ marginRight: "5px" }}
                  />
                  Loading...
                </div>
              ) : (
                (listCollection == null || listCollection.length == 0) && (
                  <div>Empty Collection</div>
                )
              )}
            </div>
          </div>
          {currentRequest != null ? (
            <div
              className="prjapi-right"
              key={`request_${currentRequest.Id || "NoId"}_${
                this._keyRequestion
              }`}
            >
              <div className="prjapi-right-top">
                <div className="input-group">
                  <div className="input-group-prepend">
                    <select
                      defaultValue={currentRequest.HttpMethod}
                      onChange={e => {
                        console.log("change", e.target.value)
                        currentRequest.HttpMethod = e.target.value
                        this._onChangeRequestion({
                          fieldName: "HttpMethod",
                          value: currentRequest.HttpMethod,
                        })
                      }}
                    >
                      <option value="GET">GET</option>
                      <option value="POST">POST</option>
                      <option value="PUT">PUT</option>
                      <option value="DELETE">DELETE</option>
                    </select>
                  </div>
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Url"
                    defaultValue={currentRequest.SubUrl}
                    onChange={e => {
                      currentRequest.SubUrl = e.target.value
                    }}
                    onBlur={e => {
                      this._onChangeRequestion({
                        fieldName: "SubUrl",
                        value: currentRequest.SubUrl,
                      })
                    }}
                  />
                  <div className="input-group-append">
                    <button
                      className="btn btn-primary"
                      type="button"
                      style={{ marginRight: "2px" }}
                      onClick={() => {
                        this.requestRunApi(currentRequest, { runAndSave: true })
                      }}
                    >
                      Save and Run
                    </button>
                    <button
                      className="btn btn-primary"
                      type="button"
                      onClick={() => {
                        this.requestRunApi(currentRequest)
                      }}
                    >
                      Run
                    </button>
                  </div>
                </div>
              </div>
              <div className="prjapi-right-request">
                <div className="a-request-header">
                  <div className="a-title">Request</div>
                  <div className="a-more">
                    <div className="a-more-item">
                      <input
                        type={"checkbox"}
                        defaultChecked={currentRequest.IsNeedAuthorization}
                        onChange={ev => {
                          currentRequest.IsNeedAuthorization =
                            ev.currentTarget.checked
                          this._onChangeRequestion({
                            fieldName: "IsNeedAuthorization",
                            value: currentRequest.IsNeedAuthorization,
                          })
                        }}
                        style={{ marginRight: "5px" }}
                      />
                      Need Authorize
                    </div>
                  </div>
                </div>
                <Tabs
                  tabsWrapperClass="body-tabs body-tabs-layout no-print"
                  transform={false}
                  showInkBar={true}
                  items={this._tabsRequest}
                  selectedTabKey={"0"}
                  onChange={index => {
                    // console.warn("onChange:", index)
                  }}
                />
              </div>
              <div className="prjapi-right-response">
                <div className="a-response-header">
                  <div className="a-title">Reponse</div>
                  <div className="a-more">
                    <div style={{ margin: "0px 5px" }}>
                      Status:{" "}
                      <span
                        style={{
                          background:
                            currentRequest.ResponeStatus == "OK"
                              ? "green"
                              : "red",
                          color: "white",
                          padding: "0px 5px",
                          borderRadius: "2px",
                        }}
                      >
                        {currentRequest.ResponeStatus}
                      </span>
                    </div>
                    <div style={{ margin: "0px 5px" }}>
                      Time: {currentRequest.ResponeMilliseconds} ms
                    </div>
                  </div>
                </div>
                <Tabs
                  tabsWrapperClass="body-tabs body-tabs-layout no-print"
                  transform={false}
                  showInkBar={true}
                  items={this._tabsResponse}
                  selectedTabKey={"0"}
                  onChange={index => {
                    // console.warn("onChange:", index)
                  }}
                />
              </div>
              {isRunning == true && (
                <div className="a-loading">
                  <i
                    className="fa fa-spinner fa-spin"
                    style={{ marginRight: "5px" }}
                  />
                  Loading...
                </div>
              )}
            </div>
          ) : currentCollection != null ? (
            <div className="prjapi-right">
              <MUIFormCollection
                key={this._keyFormCollection}
                collection={currentCollection}
                fnList={{
                  requestSaveCollection: this.requestSaveCollection,
                  requestLoginGetAccessToken: this.requestLoginGetAccessToken,
                }}
              />
            </div>
          ) : (
            <div className="prjapi-right">
              {this.state.loading == true ? (
                <div className="a-loading">
                  <i
                    className="fa fa-spinner fa-spin"
                    style={{ marginRight: "5px" }}
                  />
                  Loading...
                </div>
              ) : (
                <div>Please chose collection or request</div>
              )}
            </div>
          )}
        </div>
      </WrapLayout>
    )
  }
}

const WrapLayout = styled.div`
  padding: 1rem;
  // height: calc(100% - 1rem);
  height: 100%;
  & .prjapi-wrap {
    display: flex;
    flex-direction: row;
    // height: calc(100% - 40px);
    height: 100%;
    background: white;
    position: relative;
    & .prjapi-left {
      width: 300px;
      border: 1px solid #ced4da;
      overflow: hidden;
      & .a-header {
        border-bottom: 1px solid rgb(187, 187, 187);
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        background: #e5e5e5;
        background-image: linear-gradient(
          rgba(0, 0, 0, 0),
          rgba(0, 0, 0, 0.05)
        );
        box-shadow: inset 0 1px rgba(255, 255, 255, 0.3);
        min-height: 30px;
        color: #444;
        padding: 5px;
        & .a-back {
          cursor: pointer;
        }
      }
      & .a-lrq-empty {
        padding: 1rem;
        text-align: center;
      }
      & .a-btn-search {
        width: 24px;
        text-align: center;
        padding: 5px;
        margin: 2px;
        cursor: pointer;
      }
      & .a-list {
        max-height: calc(100% - 44px);
        max-width: 100%;
        overflow: overlay;
        overflow-x: hidden;
        & .a-header-collection {
          background: #fcfce2;
          border-bottom: 1px solid #faeecc;
          padding: 5px;
          display: flex;
          flex-direction: row;
          border-left: 2px solid #fcfce2;
          cursor: pointer;
          &.a-selected {
            border-left: 2px solid #033c73;
          }
        }
        & .a-item-collection {
          padding: 5px;
          border-bottom: 1px solid rgb(187, 187, 187);
          display: flex;
          & > .a-name {
            flex: 1;
            cursor: pointer;
          }
          & > .a-item-action {
            width: 20px;
            text-align: center;
            cursor: pointer;
          }
        }
        & .a-item-request {
          padding: 5px;
          padding-right: 20px;
          cursor: pointer;
          border-bottom: 1px solid rgb(187, 187, 187);
          overflow: hidden;
          text-overflow: ellipsis;
          width: 100%;
          position: relative;
          display: flex;
          align-items: center;
          border-left: 2px solid white;
          & > .a-method {
            font-size: 10px;
            color: green;
            font-weight: bold;
            margin-right: 2px;
            text-align: right;
            width: 30px;
            display: flex;
            align-items: center;
            justify-content: flex-end;
          }
          & > .a-item-main {
            overflow: hidden;
            text-overflow: ellipsis;
            width: 100%;
          }
          & > .a-item-action {
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            width: 20px;
            display: flex;
            align-items: center;
            justify-content: center;
          }
          &.a-selected {
            border-left: 2px solid #033c73;
            background: #ddeeff;
          }
        }
      }
    }
    & .prjapi-right {
      flex: 1;
      border: 1px solid #ced4da;
      padding: 10px;
      display: flex;
      flex-direction: column;
      overflow: hidden;
      & .a-loading {
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        background: #efefef7d;
      }
      & .prjapi-right-top {
        margin-bottom: 10px;
        & select {
          border-color: #ced4da;
          outline: none;
        }
      }
      & .prjapi-right-request {
        border: 1px solid #ced4da;
        height: 50%;
        margin-bottom: 10px;
        overflow: hidden;
        & .a-request-header {
          padding-left: 5px;
          display: flex;
          flex-direction: row;
          background: #e5e5e5;
          background-image: linear-gradient(
            rgba(0, 0, 0, 0),
            rgba(0, 0, 0, 0.05)
          );
          box-shadow: inset 0 1px rgba(255, 255, 255, 0.3);
          border-bottom: 1px solid rgb(187, 187, 187);
          & .a-title {
            font-weight: bold;
          }
          & .a-more {
            & .a-more-item {
              margin: 0px 5px;
              padding: 0px 5px;
              display: flex;
              align-items: center;
              background: rgb(242, 242, 242);
            }
          }
        }
        & .body-tabs > .RRT__tab {
          padding: 5px;
        }
      }
      & .prjapi-right-response {
        border: 1px solid #ced4da;
        height: 50%;
        overflow: hidden;
        & .a-response-header {
          padding-left: 5px;
          display: flex;
          flex-direction: row;
          background: #e5e5e5;
          background-image: linear-gradient(
            rgba(0, 0, 0, 0),
            rgba(0, 0, 0, 0.05)
          );
          box-shadow: inset 0 1px rgba(255, 255, 255, 0.3);
          border-bottom: 1px solid rgb(187, 187, 187);
          & .a-title {
            font-weight: bold;
          }
          & .a-more {
            display: flex;
            flex-direction: row;
          }
        }
        & .a-res-result {
          max-height: 100%;
          overflow: hidden auto;
          height: 100%;
          & > .react-codemirror2 {
            height: 100%;
            & > .CodeMirror-wrap {
              height: 100%;
            }
          }
        }
        & .body-tabs > .RRT__tab {
          padding: 5px;
        }
        & .RRT__container {
          height: calc(100% - 22px);
          & .RRT__panel {
            height: calc(100% - 32px);
          }
        }
      }
    }
  }
`

export default MP_PRJ_Info
